.login-wrap{
    height: 100%;
    background-image:url(../images/login-bg.png);
    background-size:cover;
    background-position: center center; 
}

.login-left{
    float: left;
    padding-top:100px;
    .font{
        font-size: 106px;
        color: #FFFFFF;
        line-height: 98px;
    }
    .money{
        font-size: 18px;
        color: #FFFFFF;
        line-height: 59px;
    }
}

.login-right{
    float: right;
    padding-top:90px;
    .login{
        background: #FFFFFF;
        box-shadow: 0px 2px 54px 0px rgba(0,21,30,0.16);
        border-radius: 8px;
        width: 460px;
        height: 530px;
        padding: 0 77px;
        dl{
            text-align: center;
            dt{
                i{
                    font-size: 100px;
                }
            }
        }
        p{
            opacity: 0.59;
            font-size: 16px;
            color: #73808E;
            height: 58px;
            border-bottom:1px solid #D0D0D0;
            padding: 9px 0;
            overflow: hidden;
            input{
                border:none;
                background: none;
                outline: none;
                height: 40px;
            }
            .checkcodebtn{
                float:right;
                width: 100px;
                height: 34px;
                text-align: center;
                line-height: 34px;
                border: 1px solid #E3E0E0;
                font-size: 16px;
                color: #FF5065;
                cursor: pointer;
            }
        }
        button{
            border:none;
            background: none;
            display: block;
            width: 100%;
            height: 44px;
            line-height: 44px;
            color:#fff;
            text-align: center;
            font-size: 16px;
            background: #7B1AD9;
            border-radius: 100px;
            cursor: pointer;
        }
    }
}